.home-wrap {
  height: 100vh;
  /* background-color: #0038a6; */
  transition: background-color 1s;
}

.home-content {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.8);
}

.home-text-animation {
  padding: 0;
  margin: 0;
  margin-top: -0.16em;
  list-style: none;
  font-family: 'Playfair Display SC', serif;
  font-size: 10.5vw;
  line-height: 0.9;
  letter-spacing: 0.04em;
}

.home-text-animation .mask {
  display: flex;
  position: relative;
  overflow: hidden;
  line-height: 1.2;
  margin-top: -0.36em;
}

/* .mask div {
	animation: text 1.4s 2 alternate both cubic-bezier(0.3, 0, 0.2, 1);
} */

.feature-imgs-wrap {
  width: 28.75vw;
  height: 35.41667vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, calc(-50% - -0.2vw));
  z-index: 2;
}

.feature-img-mask {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.feature-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transform: translate3D(0, 100%, 0);
}

.home-img-wrap {
  width: 28.75vw;
  height: 35.41667vw;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, calc(-50% - -0.2vw));
  z-index: 2;
}

.home-img-mask {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.home-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: brightness(0.8);
  /* animation: image-enter 1.4s cubic-bezier(0.215, 0.61, 0.355, 1); */
}

.feature-img.enter {
  z-index: 1;
  animation: image-enter 1.4s both cubic-bezier(0.215, 0.61, 0.355, 1);
}
.feature-img.exit {
  z-index: 0;
  animation: image-exit 1.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

@keyframes text {
  0% {
    opacity: 0;
    transform: translate3D(0, -140%, 0);
  }
  70% {
    opacity: 1;
    transform: translate3D(0, 0, 0);
  }
  100% {
    transform: translate3D(0, 0, 0);
  }
}

@keyframes image-enter {
  0% {
    transform: scale(2.2) translate3D(0, 100%, 0);
  }

  100% {
    transform: scale(1) translate3D(0, 0, 0);
  }
}

@keyframes image-exit {
  0% {
    transform: scale(1) translate3D(0, 0, 0);
  }
  100% {
    transform: scale(1.8);
  }
}
