.slider-wrap {
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.slider {
  /* --horizontal-space: 2vmax; */
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 22.5vw;
  /* margin: 0 var(--horizontal-space); */
  cursor: grab;
  user-select: none;
  z-index: 2;
}

.slider__inner {
  display: flex;
  position: relative;
}

.slide {
  overflow: hidden;
}

.slide:first-child {
  position: relative;
}

.slide:not(:first-child) {
  height: 100%;
  position: absolute;
  top: 0;
}

.slide__inner {
  width: 56vw;
  padding-top: 75.56%;
  position: relative;
  overflow: hidden;
}

.slide img {
  display: none;
}

