.slide-titles {
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: hidden;
  z-index: 3;
  pointer-events: none;
  transform: translate(-50%, -50%);
  mix-blend-mode: exclusion;
}

.slide-titles__list {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.slide-title__wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slide-title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Playfair Display SC', serif;
  font-size: 8vw;
  color: #fafafa;
  white-space: nowrap;
}

.slide-title--placeholder {
  visibility: hidden;
}

.progress {
  width: 100%;
  height: 0.25rem;
  position: fixed;
  bottom: 0;
  left: 0;
  overflow: hidden;
  pointer-events: none;
}

.progress__line {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform-origin: left;
  background-color: rgba(255, 255, 255, 0.7);
}

.progress__line:nth-child(2) {
  transform-origin: right;
}

.dom-gl {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}
