.about-img-wrap {
  display: flex;
  height: 100vh;
}

.about-img-mask {
  width: 75%;
  padding-top: 50%;
  position: relative;
  overflow: hidden;
  margin: auto;
}

.about-img-wrap .img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  filter: brightness(1.2);
}

.about-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 4vw;
}

.name {
  margin-bottom: 6vmin;
  font-size: 4.5vmin;
  font-family: 'Playfair Display SC', serif;
  font-weight: 600;
  line-height: 1.4;
  text-transform: uppercase;
}

.name .word {
  display: block;
  line-height: 1;
  position: relative;
  overflow: hidden;
  margin-bottom: 0.3em;
}

.name .char {
  display: inline-block;
  letter-spacing: 0.03em;
}

.title {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 3vh;
  letter-spacing: 0.05em;
  position: relative;
  overflow: hidden;
  font-weight: 600;
  text-transform: uppercase;
}

.title span {
  display: inline-block;
}

.about-content p {
  line-height: 1.8;
  font-weight: 400;
  font-size: 16px;
}

.bio-list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.bio-list li {
  font-size: 1.5vmin;
  position: relative;
  overflow: hidden;
  line-height: 1.4;
  margin-bottom: 0.8em;
}

.bio-list li span {
  display: inline-block;
}

.contact-nav {
  margin-top: 4vmin;
  position: relative;
  overflow: hidden;
}

.contact-nav a {
  display: inline-block;
  margin-right: 6px;
}

@keyframes bg {
  0% {
    background-color: #06aaff;
  }
  25% {
    background-color: #dd77c3;
  }

  50% {
    background-color: #43c6ff;
  }

  75% {
    background-color: #8d84e2;
  }

  100% {
    background-color: #06aaff;
  }
}
