.menu-btn-wrap {
	width: 1.4vw;
	height: 1.4vw;
	cursor: pointer;
	transform: translate3d(0px, 0px, 0px);
	position: relative;
	z-index: 100;
	margin-right: 0.5vw;
}
.menu-btn-svg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	fill: #ededed;
	opacity: 0.7;
	pointer-events: none;
}
