html {
  box-sizing: border-box;
  background: #000;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

:root {
  --base-duration: 1s;
  --base-ease: cubic-bezier(0.215, 0.61, 0.355, 1);
}

body {
  margin: 0;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 14px;
  color: #ededed;
  transition: 0.4s;
}

storng {
  font-weight: 600;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a,
button,
textarea {
  font-family: inherit;
}

.spacer {
  height: 80vh;
  background: gold;
}

.spacer.second {
  height: 80vh;
  background: pink;
}

.animated-placeholder {
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  background: #f6f7f8;
  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
  background-size: 800px 104px;
  height: 50px;
  position: relative;
}

.background-loader {
  animation: 1s placeHolderShimmer linear infinite forwards;
  background: #f6f7f9;
  background-image: linear-gradient(
    to right,
    #f6f7f9 0%,
    #e9ebee 20%,
    #f6f7f9 40%,
    #f6f7f9 100%
  );
  background-repeat: no-repeat;
  background-size: 800px 200px;
  position: relative;
}

@keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0;
  }
  100% {
    background-position: 468px 0;
  }
}

.cursor {
  display: none;
}

@media (any-pointer: fine) {
  .cursor {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    pointer-events: none;
    z-index: 1000;
  }
  /* .cursor__inner {
		fill: var(--cursor-fill);
		stroke: var(--cursor-stroke);
		stroke-width: var(--cursor-stroke-width);
	} */
}

a,
a:focus,
button,
button:focus {
  outline: none;
}

.layer {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  transform: translate3d(0, 101%, 0);
}

.intro .layer,
.layer:last-child {
  display: block;
}

.layer:nth-child(1) {
  background-color: pink;
}
.layer:nth-child(2) {
  background-color: orange;
}
.layer:nth-child(3) {
  background-color: turquoise;
}
.layer:nth-child(4) {
  background-color: plum;
}
.layer:nth-child(5) {
  background-color: greenyellow;
}

.grid {
  grid-area: 1 / 1 / 2 / 2;
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(13, 1fr);
  grid-template-rows: repeat(8, 1fr);
  grid-gap: 1vw;
  margin: auto;
  position: relative;
}

.grid::after {
  display: none;
  content: '';
  height: 6rem;
  width: 1px;
  background: #fff;
  position: absolute;
  bottom: 2rem;
  left: 50%;
}

.intro .grid::after {
  display: none;
}

.grid__item {
  background-size: cover;
  background-position: center;
  opacity: 0;
  -webkit-filter: brightness(0.7);
  filter: brightness(0.7);
  position: relative;
  overflow: hidden;
}

.grid__item--a {
  grid-area: 2 / 1 / 5 / 3;
}

.grid__item--b {
  grid-area: 6 / 2 / 9 / 5;
}

.grid__item--c {
  grid-area: 3 / 4 / 7 / 7;
}

.grid__item--d {
  grid-area: 1 / 8 / 4 / 10;
}

.grid__item--e {
  grid-area: 4 / 10 / 7 / 14;
}

.grid__item--f {
  grid-area: 6 / 9 / 9 / 12;
}

.project-name {
  mix-blend-mode: difference;
  filter: brightness(0.9);
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
}

.project-name > div {
  white-space: nowrap;
  font-family: 'Playfair Display SC', serif;
  font-size: 10vw;
}

.project-name .char {
  display: inline-block;
}

.project-name .char.empty {
  width: 0.3em;
}

.back {
  display: flex;
  align-items: center;
  color: white;
  text-decoration: none;
  font-size: 16px;
  font-weight: 400;
  position: fixed;
  top: 3vh;
  left: 1vw;
  overflow: hidden;
  background: none;
  border: none;
  z-index: 10;
  cursor: pointer;
}

.back svg {
  margin-right: 8px;
}

body {
  --cursor-stroke: #fafafa;
  --cursor-fill: #000;
  --cursor-stroke-width: 1px;
}

.cursor {
  display: none;
}

@media (any-pointer: fine) {
  .cursor {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    pointer-events: none;
  }
  .cursor__inner {
    /* fill: var(--cursor-fill); */
    fill: none;
    stroke: var(--cursor-stroke);
    stroke-width: var(--cursor-stroke-width);
    opacity: 0.3;
  }
}

.mobile {
  display: none;
}

@media (orientation: portrait) and (max-width: 720px) {
  html,
  body {
    position: relative;
    overflow: hidden;
  }

  .mobile {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    background: #000;
    padding: 4vmin;
    text-transform: uppercase;
    letter-spacing: 0.01em;
  }

  .mobile h1 {
    font-size: 9vmin;
    font-weight: 400;
    margin-bottom: 0.8em;
    font-family: 'Playfair Display SC', serif;
  }

  .mobile img {
    display: inline-block;
    max-width: 100%;
    height: auto;
  }
}
