.nav {
  /* display: none; */
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
}

.nav-link {
  position: absolute;
  font-size: 1.5vmin;
  letter-spacing: 0.06em;
  text-decoration: none;
  color: #fff;
  animation: nav-enter var(--base-duration) var(--base-ease);
}

@keyframes nav-enter {
  from {
    opacity: 0;
    transform: translateY(25px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.nav-link:first-child {
  display: flex;
  align-items: center;
  top: 6vh;
  left: 4vw;
}

.nav-link:nth-child(2) {
  top: 7vh;
  right: 4vw;
}

.nav-link:last-child {
  /* bottom: 8vh; */
  right: 4vw;
}

.nav-link.contact {
  display: flex;
  align-items: center;
}

.nav-link.contact a {
  display: inline-block;
  margin-left: 8px;
}
