.projects-wrap {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  background: #111;
}

.projects-container {
  display: flex;
  flex-wrap: nowrap;
  width: 82.43vw;
  position: absolute;
  top: 2vw;
  left: 2vw;
  bottom: 2vw;
  overflow: hidden;
}

.projects-list {
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: relative;
  overflow: auto;
}

.project-item {
  position: relative;
  flex: 0 0 20%;
  height: 100%;
  padding: 2vw;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  /* transform: translate3d(-500%, 0, 0); */
  background: #f5f5f5;
  cursor: pointer;
}

.projects-close {
  position: absolute;
  top: calc(50% - 3.125vw / 2);
  left: 91.14vw;
  width: 5.2vw;
  height: 3.125vw;

  display: flex;
  align-items: center;
  justify-content: center;
}

.projects-close .line {
  position: absolute;
  top: 50%;
  left: -5.2vw;
  height: 1px;
  width: 5.3125vw;
  pointer-events: none;
  background: #f5f5f5;
}

.projects-close a {
  color: #f5f5f5;
  letter-spacing: 0.06em;
  text-decoration: none;
}

.project-number {
  margin-top: 24vh;
}

.project-number-inner {
  display: inline-block;
  position: relative;
  overflow: hidden;
  font-size: 3.4vw;
  color: #ce2245;
  font-weight: 600;
  padding-bottom: 1em;
}

.project-content {
  margin-top: 10vh;
}

.project-content h2 {
  font-weight: 600;
  margin: 0;
  font-size: 0.8vw;
  color: rgba(0, 0, 0, 0.7);
  text-transform: uppercase;
}
.project-content h4 {
  font-weight: 400;
  font-size: 0.7vw;
  line-height: 2;
  margin: 0;
  color: rgba(0, 0, 0, 0.8);
}

.project-content small {
  font-size: 0.7vw;
  color: rgba(0, 0, 0, 0.6);
}

.img-ratio__wrap {
  width: 100%;
  height: 0;
  padding-top: 56%;
  margin: 4vmin 0;
  position: relative;
  overflow: hidden;
}
.img-ratio__inner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #eee;
}
.img-ratio__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity var(--base-duration), transform 10s;
}

.project-item:hover .img-ratio__img {
  transform: scale(1.2);
}
